"use client";

import { useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import TextArea from "@/components/form/input/TextArea";
import Label from "@/components/form/Label";
import { addGymAnnouncement } from "@/components/community/notificationsMock";

export default function AdminAnnouncementComposer() {
  const [title, setTitle] = useState("");
  const [body, setBody] = useState("");
  const [ok, setOk] = useState<string | null>(null);

  const onPublish = () => {
    const trimmed = body.trim();
    if (!trimmed) return;

    addGymAnnouncement({
      title: title.trim() || undefined,
      body: trimmed,
    });

    setTitle("");
    setBody("");
    setOk("Anuncio publicado (mock).");
    setTimeout(() => setOk(null), 1500);
  };

  return (
    <div className="rounded-2xl border border-gray-200 bg-white p-5 dark:border-gray-800 dark:bg-white/[0.03]">
      <div className="flex flex-col gap-1">
        <h3 className="text-lg font-semibold text-gray-800 dark:text-white/90">
          Publicar anuncio
        </h3>
        <p className="text-sm text-gray-500 dark:text-gray-400">
          Visible para atletas (community + campanita).
        </p>
      </div>

      <div className="mt-5 grid grid-cols-1 gap-4">
        <div>
          <Label>Título (opcional)</Label>
          <Input
            placeholder="Ej: Mañana el box cierra por feriado"
            defaultValue={title}
            onChange={(e) => setTitle(e.target.value)}
          />
        </div>

        <div>
          <Label>Mensaje</Label>
          <TextArea
            rows={4}
            placeholder="Escribí el anuncio…"
            defaultValue={body}
            onChange={(value) => setBody(value)}
          />
        </div>

        <div className="flex items-center justify-between gap-3">
          <span className="text-sm text-success-600">
            {ok ? ok : ""}
          </span>
          <Button variant="primary" onClick={onPublish} disabled={!body.trim()}>
            Publicar
          </Button>
        </div>
      </div>
    </div>
  );
}
