"use client";

import NotificationItem from "./NotificationItem";
import { notificationsMock } from "./notificationsMock";

function parseDate(input: unknown): Date | null {
  if (!input) return null;
  if (input instanceof Date) return input;
  if (typeof input === "number") return new Date(input);
  if (typeof input === "string") {
    const d = new Date(input);
    return isNaN(d.getTime()) ? null : d;
  }
  // @ts-expect-error - allow common shapes
  const maybe = (input as any)?.date ?? (input as any)?.createdAt ?? (input as any)?.at;
  if (typeof maybe === "string" || typeof maybe === "number") {
    const d = new Date(maybe);
    return isNaN(d.getTime()) ? null : d;
  }
  return null;
}

function isSameDay(a: Date, b: Date) {
  return (
    a.getFullYear() === b.getFullYear() &&
    a.getMonth() === b.getMonth() &&
    a.getDate() === b.getDate()
  );
}

export default function NotificationsFeed() {
  const now = new Date();
  const cutoff = new Date();
  cutoff.setDate(cutoff.getDate() - 30);

  const normalized = notificationsMock
    .map((n) => {
      const d = parseDate(n) ?? now; // fallback
      return { n, d };
    })
    .filter(({ d }) => d >= cutoff)
    .sort((a, b) => b.d.getTime() - a.d.getTime()); // newest first

  const today = normalized.filter(({ d }) => isSameDay(d, now));
  const yesterdayDate = new Date(now);
  yesterdayDate.setDate(yesterdayDate.getDate() - 1);
  const yesterday = normalized.filter(({ d }) => isSameDay(d, yesterdayDate));
  const older = normalized.filter(
    ({ d }) => !isSameDay(d, now) && !isSameDay(d, yesterdayDate)
  );

  return (
    <div className="space-y-4">
      {today.length > 0 && (
        <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
          <div className="border-b border-gray-200 px-5 py-4 dark:border-gray-800">
            <h3 className="text-base font-semibold text-gray-800 dark:text-white/90">
              Hoy
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Últimos 30 días
            </p>
          </div>
          <div className="divide-y divide-gray-200 dark:divide-gray-800">
            {today.map(({ n }) => (
              <NotificationItem key={(n as any).id ?? JSON.stringify(n)} item={n as any} />
            ))}
          </div>
        </div>
      )}

      {yesterday.length > 0 && (
        <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
          <div className="border-b border-gray-200 px-5 py-4 dark:border-gray-800">
            <h3 className="text-base font-semibold text-gray-800 dark:text-white/90">
              Ayer
            </h3>
          </div>
          <div className="divide-y divide-gray-200 dark:divide-gray-800">
            {yesterday.map(({ n }) => (
              <NotificationItem key={(n as any).id ?? JSON.stringify(n)} item={n as any} />
            ))}
          </div>
        </div>
      )}

      {older.length > 0 && (
        <div className="rounded-2xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.03]">
          <div className="border-b border-gray-200 px-5 py-4 dark:border-gray-800">
            <h3 className="text-base font-semibold text-gray-800 dark:text-white/90">
              Anteriores
            </h3>
          </div>
          <div className="divide-y divide-gray-200 dark:divide-gray-800">
            {older.map(({ n }) => (
              <NotificationItem key={(n as any).id ?? JSON.stringify(n)} item={n as any} />
            ))}
          </div>
        </div>
      )}

      {normalized.length === 0 && (
        <div className="rounded-2xl border border-gray-200 bg-white p-6 text-sm text-gray-500 dark:border-gray-800 dark:bg-white/[0.03] dark:text-gray-400">
          No hay notificaciones en los últimos 30 días.
        </div>
      )}
    </div>
  );
}
